import React, { useState, useEffect } from 'react'; 
import axios from 'axios'; 
import 'bootstrap/dist/css/bootstrap.min.css';
import { useLocation, useNavigate } from 'react-router-dom'; 

const Home = () => {  
    const navigate = useNavigate();
    const [ userData, setUserData]  = useState();
    const location = useLocation();
    const params = new URLSearchParams(location.search);
    const idUs = params.get("idUs");

    const handleClick = () => {
        navigate('/logs'); // Redirige a la pgina de logs
      };
    
    useEffect(() => {
        if(idUs){
            handleGetUser();
        }
    }, [idUs]);
    
    const handleGetUser = async () => { 
        try{
            const res = await axios.get(`http://localhost:5001/getInfo?idUs=${idUs}`); 
            setUserData(res.data.user); 
            }catch(error){
            alert("Error al obtener la informacin del usuario");
            }

    }; 
    
    return (
        <div className="d-flex justify-content-center align-items-center bg-light mt-3 pt-2 mb-4">
      <div className="card shadow-lg p-6" style={{ width: '600px', textAlign: 'center', position: 'relative' }}>
        <img 
          src="https://ambystomadev.uteq.edu.mx/Content/assets/Logo_uteq_color.png" 
          alt="UTEQ Logo" 
          style={{ position: 'absolute', top: '40px', left: '50px', width: '250px' }}
        />
        <div className="card-body" style={{marginTop: '160px'}}>
          <h2>Universidad Tecnolgica de Quertaro</h2>
          <br />
          <h4>Seguridad Informtica</h4>
          <br />
          <h5>Conceptos Bsicos de la Seguridad</h5>
          <br />
          <h5>Grupo: IDSG11</h5>
          <br />
          <h5>Alumn@: Salinas Jimnez Mara Dolores</h5>
          <br />
          <h5>Grado: Tcnico Superior Universitario (TSU)</h5>
          <br />
          <h5 style={{marginBottom: '200px'}}>Maestro: Agustn Buenrostro Rico</h5>
          <hr style={{height:'3px', backgroundColor: 'black', border: 'none' }} />
          <h4 className="mt-3" style={{textAlign: 'center', paddingTop: '100px'}}>Descripcin de la app:</h4> 
          <p className="text-justify" style={{textAlign: 'justify', paddingBottom: '300px', paddingLeft: '30px', paddingRight: '30px'}}>
            Esta aplicacin web est diseada para gestionar el acceso seguro de usuarios mediante autenticacin JWT con MFA y registrar logs detallados de actividad en una base de datos.
            Cuenta con dos servidores backend que manejan las mismas funcionalidades, con la diferencia de que uno incorpora un sistema de Rate Limit para limitar solicitudes, mientras que el otro no.
            Adems, el sistema incluye un frontend interactivo con visualizacin de datos y mtricas de logs.
          </p>
          <button className="btn btn-warning mt-3" type="button" onClick={handleClick}>
            Ver Logs
          </button>
        </div>
      </div>
    </div>
      );
};

export default Home;
